			  <span v-if="userAddress == dataStat3[2].toLowerCase()">第3名</span>
			  <span v-if="(userAddress != dataStat3[0].toLowerCase())&&(userAddress != dataStat3[1].toLowerCase())&&(userAddress != dataStat3[2].toLowerCase())">未上榜</span>
